import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			ctx: this.$ctx,
			dataList: [],
			entrance: '',
			companyId: '',
			noData: '',
			contentText: '正在加载...',
			pageNumber: 1,
			obj: {},
			type: ''
		};
	},
	onBackPress() {
		if (this.type !== '') {
			this.type = '';
			return true;
		}
	},
	onLoad(option) {
		this.entrance = option.entrance;
		this.companyId = option.companyId;
		this.getDataList();
	},
	onShow() {
		console.log('this.companyId', this.companyId);
		this.noData = 10;
		this.dataList = [];
		this.contentText = '正在加载...';
		this.pageNumber = 1;
		this.getDataList();
	},
	onReachBottom: function() {
		this.pageNumber++;
		console.log(this.pageNumber);
		this.getDataList();
	},
	methods: {
		getDataList() {
			if (this.noData < 10) {
				this.contentText = '没有更多数据了';
				return;
			}
			this.contentText = '正在加载...';
			this.$getJson(
				'/api/v2/vue/sqPlus/resources/resourcesList.jsp', {
					companyId: this.companyId || 203,
					pageNumber: this.pageNumber,
					isManager:1
				},
				'POST',
				res => {
					console.log('----------------', res);

					this.dataList = [...this.dataList, ...res.data]
					this.noData = res.data.length;
					if (this.noData < 10) {
						this.contentText = '没有更多数据了';
					} else {
						this.contentText = '上拉显示更多';
					}
				}
			);
		},
		visual(item) {
			this.$getJson('/api/v2/vue/sqPlus/resources/resourcesIsExplosive.jsp', {
				data: JSON.stringify({
					resourcesId: item.id
				})
			}, 'POST', res => {
				this.dataList.forEach((a, b) => {
					if (a.id == item.id) {
						if (a.isExplosive == 0) {
							a.isExplosive = 1;
						} else {
							a.isExplosive = 0;
						}
					}
				});
			});
		},
		togglePopup(type, item) {
			console.log(item);
			this.type = type;
			this.obj = item;
		},
		delRes() {
			console.log(this.obj);
			this.$getJson('/jsp/api/resources/delResources.jsp', {
				resourcesId: this.obj.id
			}, 'POST', res => {
				this.dataList.forEach((a, b) => {
					if (a.id == this.obj.id) {
						this.dataList.splice(b, 1);
						this.type = '';
						this.obj = {};
					}
				});
			});
		},
		addShop(id) {
			uni.navigateTo({
				url: '/pages/user/shopList/addShop?isCard=1&resourcesId=' + id
			});
		},
		shopDetails(id) {
			uni.navigateTo({
				url: '/pages/shopList/shopDetails?isCard=1&resourcesId=' + id
			});
		}

	}
};
